.cld_container {
  /* width: 30%; */
  width: 40%;
  margin: 5% auto;
  box-shadow: 0px 1px 5px 1px #dddddd;
  padding: 1%;
  border-radius: 5px;
  background: #f1f1f1;
}

.cld_btnAlign {
  display: flex;
  justify-content: space-between;
  margin: 6px 0;
}

.cld_btnAlign button {
  cursor: pointer;
}

.cld_showDays {
  display: flex;
  font-size: 18px;
  font-weight: bold;
}

.cld_showDays select:first-child {
  margin-right: 10px;
}

.cld_highlightFirstNum {
  color: #ffffff;
  background-color: #3ecf8e;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  padding-inline-end: 10px;
}

.cld_highlightNum {
  background-color: #3ecf8e;
  color: #ffffff;
}

.cld_highlightLastNum {
  color: #ffffff;
  background-color: #3ecf8e;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  padding-inline-start: 10px;
}

.cld_highlightNumCircle {
  background-color: #3ecf8e;
  border-radius: 20px;
  color: #ffffff;
  margin: 0 5px;
}

.cld_inrange {
  background-color: #b8ead4;
}

td:hover .cld_inrangeLastIndex {
  background-color: #b8ead4;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}

td:hover .cld_inrangeFirstIndex {
  background-color: #b8ead4;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}

select {
  border-color: #fff;
  padding: 5px 2px;
  font-size: 15px;
  border-radius: 3px;
  outline: none;
}

option {
  font-size: small;
}

select:hover {
  cursor: pointer;
}

/* table */

table {
  border-collapse: collapse;
  width: 100%;
  background: #ffffff;
  table-layout: fixed;
}

td,
th {
  /* border: 1px solid #f1f1f1; */
  border: 1px solid #a5a5a5;
  padding: 8px;
  text-align: center;
}

th {
  font-weight: 500;
}

td {
  padding: 8px 0px;
  cursor: pointer;
  position: relative;
}

.cld_cellHover {
  position: relative;
  padding: 3px;
  margin: 15px 0;
}

.cld_cellSingleMultiple {
  margin: 0 5px;
}

td:nth-child(n + 7) div.cld_highlightNum,
td:nth-child(n + 7) div.cld_highlightFirstNum,
td:nth-child(n + 7) div.cld_highlightLastNum,
td:nth-child(n + 7) div.cld_inrange, 
td:nth-child(n + 7) div.cld_disablebgColor {
  border-bottom-right-radius: 10px;
  border-top-right-radius: 10px;
  margin-right: 5px;
}

td:nth-child(7n + 1) div.cld_highlightNum,
td:nth-child(7n + 1) div.cld_highlightLastNum,
td:nth-child(7n + 1) div.cld_highlightFirstNum,
td:nth-child(7n + 1) div.cld_inrange,
td:nth-child(7n + 1) div.cld_disablebgColor {
  border-bottom-left-radius: 10px;
  border-top-left-radius: 10px;
  margin-left: 5px;
}

td:hover > .cld_cellHover.cld_cellactive:after {
  content: "";
  position: absolute;
  top: -2px;
  right: 0px;
  bottom: -2px;
  left: 0px;
  border-radius: 20px;
  border: 1px solid #008000;
}

.cld_disableDate {
  color: #c4c4c4;
}

.cld_disablebgColor {
  color: #ffffff;
  background: #b4b4b4;
}

/* slots */
.cld_slots {
  font-size: 13px;
  width: 25px;
  height: 22px;
  padding-inline-start: 4px;
  font-weight: 500;
  box-shadow: 1px 1px 5px 0px rgb(38 128 235 / 44%);
  /* color: #6b6b6b; */
}

.cld_availableSlots{
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 0 0 16px 0;
  color: #2680eb;
  background: #a3c4ec 0 0 no-repeat padding-box;
  text-align: left;
  /* z-index: 1; */
}

.cld_totalSlots{
  position: absolute;
  bottom: 0;
  right: 0;
  border-radius: 16px 0 0 0;
  background: #a3c4ec 0 0 no-repeat padding-box;
  color: #2680eb;
  /* background: #bbbaea; */
}